@import url('https://fonts.googleapis.com/css2?family=Barlow&display=swap');

.footer {
    background-color: var(--secondary);
    padding: 0px 0 10px 0;
    text-align: center;
    margin-top: 150px;
  }
  
  .footer-content {
    color: var(--primary);
    align-items: center;
    text-align: center;
    padding: 60px 0 0 0;
    background-image: url(../../../src/assets/JACE-logo.png);
    background-size: 125px;
    background-position: center top;
    background-repeat: no-repeat;
  }

  .footer-content p {
    padding-top: 60px;
    font-family: 'Barlow', sans-serif;
  }
  
  .footer-content a {
    text-decoration: none;
    font-weight: 400;
    color: var(--primary);
    font-family: 'Barlow', sans-serif;
  }
  
  .footer-content a:hover {
    text-decoration: underline;
  }
  
  /* Tablet view */
  @media (max-width: 768px) {
    .footer-content h2 {
      font-size: 24px;
    }
  
    .footer-content p {
      font-size: 16px;
    }
  }
  
  /* Phone view */
  @media (max-width: 480px) {
    .footer-content h2 {
      font-size: 20px;
    }
  
    .footer-content p {
      font-size: 14px;
    }
  }
  