:root {
  --primary: #242424;
  --secondary: #F1AE71;
  --tertiary: #B12704;
  --light: #EBEADD;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  background-color: #EBEADD;
  background-color: var(--light);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: #272f32;
  color: var(--dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: #B12704;
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: #242424;
  background: var(--primary);
}

header a {
  color: #EBEADD;
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #F1AE71;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid #272f32;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid #F1AE71;
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: rgb(228, 46, 1);
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}

.card {
  position: relative;
  box-sizing: border-box;
  padding: 20px;
  background-color: var(--light);
  border: 3px solid var(--secondary);
  border-radius: 5px;
  margin: 10px;
  transition: all 0.3s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: calc(25% - 20px); 
  max-width: calc(25% - 20px);
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 8px 12px rgba(0.15, 0.15, 0.15, 0.15);
}

.card:hover a {
  text-decoration: none;
}

.img-container {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.img-container img {
  width: 100%;
  height: auto;
  display: block;
}

.product-info {
  color: black;
  font-family: 'Barlow', sans-serif;
}

.product-name {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 8px;
  font-family: 'Barlow', sans-serif;
}

.product-price {
  font-size: 16px;
  margin-bottom: 16px;
  font-family: 'Helvetica', sans-serif;
}

/* Tablet devices (768px and up) */
@media (min-width: 768px) and (max-width: 1024px) {
  .card {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(50% - 20px); /* 50% width minus margins */
    max-width: calc(50% - 20px);
  }
}

/* Mobile devices (767px and below) */
@media (max-width: 767px) {
  .card {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(100% - 20px); /* 100% width minus margins */
    max-width: calc(100% - 20px);
  }
}
.category-menu {
  font-family: 'Barlow', sans-serif;
  color: var(--primary);
  padding: 0 0 10px 10px;
  margin-top: 100px;
}

.whole-category {
  width: 100%;
}

.category-btn {
    cursor: pointer;
    background-color: var(--secondary);
    color: var(--primary);
    font-family: 'Barlow', sans-serif;
    font-weight: 600;
    font-size: 16px;
    padding: 10px 25px;
    margin: 0 5px 5px 10px;
    transition: background-color 0.3s ease, -webkit-transform 0.3s ease;
    transition: background-color 0.3s ease, transform 0.3s ease;
    transition: background-color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
    border-radius: 3px;
  }

.category-btn:hover {
    background-color: var(--secondary);
    transition: background-color 0.3s ease, -webkit-transform 0.3s ease;
    transition: background-color 0.3s ease, transform 0.3s ease;
    transition: background-color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 40%;
  min-height: 20%;
  background-color: var(--light);
  overflow: auto;
  padding: 2rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-bottom-left-radius: .5rem;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  position: fixed;
  top: 2%;
  right: 1%;
  font-size: 2rem;
  cursor: pointer;
  background-color: var(--secondary);
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

.total {
  border-top: 1px solid var(--dark);
  margin-top: 16px;
  padding-top: 8px;
}

@media screen and (max-width: 920px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}

.detail-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .back-to-products-btn {
    cursor: pointer;
    background-color: darkorange;
    border: none;
    color: white;
    font-family: Arial, sans-serif;
    font-size: 14px;
    font-weight: bold;
    padding: 10px 20px;
    margin: 10px;
    transition: background-color 0.3s ease;
  }
  
  .back-to-products-btn:hover {
    background-color: orange;
  }
  
  .product-name {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin: 20px 0;
  }
  
  .product-description {
    font-size: 18px;
    text-align: center;
    margin: 0 20px 20px 20px;
  }
  
  .image-container {
    position: relative;
    width: 250px;
    height: 250px;
    border-radius: 10px;
    overflow: hidden;
    border: 5px solid white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .product-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0 0 20px 0;
  }
  
  .actions {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .product-price {
    font-size: 18px;
  }
  
  .add-to-cart-btn,
  .remove-from-cart-btn {
    cursor: pointer;
    background-color: darkorange;
    border: none;
    color: white;
    font-family: Arial, sans-serif;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 20px;
    transition: background-color 0.3s ease;
  }
  
  .add-to-cart-btn:hover {
    background-color: orange;
  }
  .remove-from-cart-btn:hover {
    background-color: orange;
  }
  
  .remove-from-cart-btn {
    background-color: darkred;
  }
  
  .remove-from-cart-btn:hover {
    background-color: red;
  }


@media (max-width: 768px) {
    .product-image {
      width: 300px;
      height: 300px;
    }
  
    .product-info {
      font-size: 14px;
    }
  
    .product-name {
      font-size: 16px;
    }
  
    .add-to-cart-btn,
    .remove-from-cart-btn {
      font-size: 12px;
      padding: 8px 16px;
    }
  }
  
  @media (max-width: 480px) {
    .product-image {
      width: 200px;
      height: 200px;
    }
  
    .product-info {
      font-size: 12px;
    }
  
    .product-name {
      font-size: 14px;
    }
  
    .add-to-cart-btn,
    .remove-from-cart-btn {
      font-size: 10px;
      padding: 6px 12px;
    }
  }
  
.links-to-go {
    color: var(--tertiary);
    margin: 0px;
    padding: 0px;
}
.signup {
    text-align: center;
    margin: 100px auto 0 auto;
    width: 400px;
    padding: 30px 0 60px 0;
    font-family: 'Barlow', sans-serif;
}

form {
    margin: 0 auto;
    width: 400px;
    font-family: 'Barlow', sans-serif;
}

label {
    display: flex;
    flex-direction: row;
    width: 100%;
}

input {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-decoration: none;
    border: none;
    background-color: white;
    padding: 10px 25px;
}

.submit {
    width: 100%;
    margin: 10px 0 0 0;
    border-radius: 3px;
    background-color: var(--secondary);
    padding: 10px 25px;
}
.nav-header {
    background-color: var(--secondary);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 1rem;
  }
  
  .nav-header h1 {
    color: var(--primary);
    text-decoration: none;
    margin: 5px 0 0 0;
    font-family: 'Bebas Neue', 'Oswald', Helvetica, sans-serif;
    font-size: 3rem;
  }

  .nav-header a {
    color: var(--primary);
    text-decoration: none;
    margin: 0px;
    padding: 0px;
  }

  .nav-header ul {
    margin-right: 5rem;
    list-style: none;
    font-family: 'Barlow', sans-serif;
    font-weight: 200;
  }

  .nav-header ul li {
    font-size: 1.3rem;
    padding: 0.5rem;
  }
  
  .logo-container {
    display: flex;
    align-items: center;
  }
  
  .logo-circle {
    width: 50px;
    height: 50px;
    background-image: url(/static/media/logo.9e197f2f.png);
    background-position: center;
    background-size: 60px;
    margin: 5px 0 0 60px;
  }
  
  .nav-links {
    display: flex;
    margin: 0;
  }
  
  @media screen and (max-width: 768px) {
    .nav-links {
      font-size: 0.9rem;
    }
  }
  
  @media screen and (max-width: 480px) {
    .nav-links {
      font-size: 0.8rem;
    }
  }
  
.footer {
    background-color: var(--secondary);
    padding: 0px 0 10px 0;
    text-align: center;
    margin-top: 150px;
  }
  
  .footer-content {
    color: var(--primary);
    align-items: center;
    text-align: center;
    padding: 60px 0 0 0;
    background-image: url(/static/media/JACE-logo.d58fa865.png);
    background-size: 125px;
    background-position: center top;
    background-repeat: no-repeat;
  }

  .footer-content p {
    padding-top: 60px;
    font-family: 'Barlow', sans-serif;
  }
  
  .footer-content a {
    text-decoration: none;
    font-weight: 400;
    color: var(--primary);
    font-family: 'Barlow', sans-serif;
  }
  
  .footer-content a:hover {
    text-decoration: underline;
  }
  
  /* Tablet view */
  @media (max-width: 768px) {
    .footer-content h2 {
      font-size: 24px;
    }
  
    .footer-content p {
      font-size: 16px;
    }
  }
  
  /* Phone view */
  @media (max-width: 480px) {
    .footer-content h2 {
      font-size: 20px;
    }
  
    .footer-content p {
      font-size: 14px;
    }
  }
  
